/*******************************************************************************
//
//
//
//  #BOX-SIZING
//
//
//
*******************************************************************************/

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/*******************************************************************************
//
//
//
//  #NORMALIZE
//
//
//
*******************************************************************************/

/*! normalize.css v4.0.0 | MIT License | github.com/necolas/normalize.css */

progress,
sub,
sup {
  vertical-align: baseline;
}

button,
hr,
input,
select {
  overflow: visible;
}

[type=checkbox],
[type=radio],
legend {
  box-sizing: border-box;
  padding: 0;
}

html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
progress,
video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden],
template {
  display: none;
}

a {
  background-color: transparent;
}

a:active,
a:hover {
  outline-width: 0;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

dfn {
  font-style: italic;
}

h1 {
  font-size: 2em;
  margin: .67em 0;
}

mark {
  background-color: #ff0;
  color: #000;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

img {
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

code,
kbd,
pre,
samp {
  font-family: monospace,monospace;
  font-size: 1em;
}

figure {
  margin: 1em 40px;
}

hr {
  box-sizing: content-box;
  height: 0;
}

button,
input,
select,
textarea {
  font: inherit;
  margin: 0;
}

optgroup {
  font-weight: 700;
}

button,
select {
  text-transform: none;
}

[type=button],
[type=reset],
[type=submit],
button {
  cursor: pointer;
}

[disabled] {
  cursor: default;
}

[type=reset],
[type=submit],
button,
html [type=button] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

button:-moz-focusring,
input:-moz-focusring {
  outline: ButtonText dotted 1px;
}

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em;
}

legend {
  color: inherit;
  display: table;
  max-width: 100%;
  white-space: normal;
}

textarea {
  overflow: auto;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
}

[type=search]::-webkit-search-cancel-button,
[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/*******************************************************************************
//
//
//
//  #HTML
//
//
//
*******************************************************************************/

:root {
  --color-black: #080808;
  --color-grey: #939393;
  --color-greyLight: #a9a9a9;
  --color-greyLighter: #f1f1f1;
  --color-slate: #47474d;
  --color-white: #fff;
  --color-primary: #fff;
  --color-secondary: #080808;
  --color-tertiary: #939393;
  --color-accent: gold;
  --color-background: #080808;
  --color-border: #939393;
  --color-border-focus: #080808;
  --color-button-background: gold;
  --color-button-text: #080808;
  --color-button-background-active: gold;
  --color-button-text-active: #080808;
  --color-input-background: #fff;
  --color-input-placeholder: #939393;
  --color-jumbotron-background: 8, 8, 8;
  --color-text: #fff;
  --color-text-link: gold;
}

[data-theme="light"] {
  --color-primary: #080808;
  --color-secondary: #fff;
  --color-tertiary: #47474d;
  --color-accent: blue;
  --color-background: #f1f1f1;
  --color-border: #47474d;
  --color-border-focus: #080808;
  --color-button-background: gold;
  --color-button-text: #080808;
  --color-button-background-active: gold;
  --color-button-text-active: #080808;
  --color-input-background: #fff;
  --color-input-placeholder: #47474d;
  --color-jumbotron-background: 241, 241, 241;
  --color-text: #080808;
  --color-text-link: blue;
}

* {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

html {
  min-height: 100%;
  font-size: 80%;
  line-height: 1.5;
  scroll-behavior: smooth;
}

html.is-disable-animations * {
  transition: none !important;
}

html.is-disable-scroll {
  height: 100vh;
  overflow: hidden;
}

/*******************************************************************************
//
//
//
//  #BODY
//
//
//
*******************************************************************************/

body {
  font-size: 20px;
  font-size: 1.25rem;
  line-height: 30px;
  line-height: 1.875rem;
  min-height: 100%;
  color: var(--color-text);
  font-family: "Inconsolata", sans-serif;
  background-color: var(--color-background);
  overflow-x: hidden;
  overflow-y: scroll;
}

/*******************************************************************************
//
//
//
//  #FORM
//
//
//
*******************************************************************************/

/*******************************************************************************
//
//  #FORM
//
*******************************************************************************/

form {
  margin: 0 0 37.5px;
}

/*******************************************************************************
//
//  #FIELDSET
//
*******************************************************************************/

fieldset {
  padding: 22.5px;
  margin: 0 0 37.5px;
}

/*******************************************************************************
//
//  #LEGEND
//
*******************************************************************************/

legend {
  display: block;
  padding: 0 22.5px;
}

/*******************************************************************************
//
//  #LABEL
//
*******************************************************************************/

label {
  display: block;
  margin: 0 0 11.25px;
}

/*******************************************************************************
//
//  #PLACEHOLDER
//
*******************************************************************************/

::-webkit-input-placeholder {
  color: var(--color-input-placeholder);
}

::-moz-placeholder {
  color: var(--color-input-placeholder);
}

:-ms-input-placeholder {
  color: var(--color-input-placeholder);
}

input:-moz-placeholder {
  color: var(--color-input-placeholder);
}

/*******************************************************************************
//
//  #INPUT AND TEXTAREA
//
*******************************************************************************/

input,
textarea {
  padding: 0.25em 0.5em;
  border: 1px solid var(--color-border);
}

input:focus,
textarea:focus {
  border-color: var(--color-border-focus);
  outline: 0;
}

input[type='email'],
input[type='url'],
input[type='search'],
input[type='tel'],
input[type='text'] {
  appearance: none;
  width: 100%;
}

input[type='radio']:focus,
input[type='checkbox']:focus {
  outline: none;
}

textarea {
  appearance: none;
  width: 100%;
  resize: none;
}

/*******************************************************************************
//
//  #FILE
//
*******************************************************************************/

input[type='file']:focus {
  outline: none;
}

/*******************************************************************************
//
//  #SELECT
//
*******************************************************************************/

select {
  width: 100%;
}

select[multiple],
select[size] {
  height: auto;
}

select:focus {
  outline: none;
}

/*******************************************************************************
//
//  #BUTTON
//
*******************************************************************************/

button:focus {
  outline: 1px dotted var(--color-accent);
  outline-offset: 4px;
}

/*******************************************************************************
//
//
//
//  #HORIZONTAL REFERENCE
//
//
//
*******************************************************************************/

hr {
  height: 1px;
  margin: 60px 0;
  background: var(--color-border);
  border: 0;
  outline: 0;
}

/*******************************************************************************
//
//
//
//  #IMAGE
//
//
//
*******************************************************************************/

img {
  max-width: 100%;
  height: auto;
  border: 0;
  vertical-align: middle;
}

figure {
  margin: 0 0 37.5px;
}

picture,
svg {
  display: block;
}

/*******************************************************************************
//
//
//
//  #LIST
//
//
//
*******************************************************************************/

/*******************************************************************************
//
//  #ORDERED AND UNORDERED LISTS
//
*******************************************************************************/

ol,
ul {
  padding-left: 0;
  margin: 0 0 37.5px 1.125em;
}

ol li:not(:last-child),
ul li:not(:last-child) {
  margin-bottom: 15px;
}

ol ol,
ol ul,
ul ol,
ul ul {
  margin-top: 0.312em;
  margin-bottom: 0;
}

/*******************************************************************************
//
//  #DEFINITION LISTS
//
*******************************************************************************/

dd {
  margin-left: 37.5px;
}

dl {
  margin: 0 0 37.5px;
}

dt {
  font-weight: 700;
}

/*******************************************************************************
//
//
//
//  #TABLE
//
//
//
*******************************************************************************/

table {
  margin: 0 0 37.5px;
  border-bottom: 1px solid var(--color-border);
  border-right: 1px solid var(--color-border);
  border-collapse: collapse;
  border-spacing: 0;
  table-layout: fixed;
  overflow-x: auto;
}

th,
td {
  padding: 0.5em;
  text-align: left;
  border-top: 1px solid var(--color-border);
  border-left: 1px solid var(--color-border);
  vertical-align: middle;
}

caption {
  padding: 0.5em;
  border-bottom: 0;
}

/*******************************************************************************
//
//
//
//  #TYPOGRAPHY
//
//  [1] https://hugogiraudel.com/2020/05/18/using-calc-to-figure-out-optimal-line-height/
//
//
//
*******************************************************************************/

/*******************************************************************************
//
//  #HEADINGS
//
*******************************************************************************/

.h1,
h1,
.h2,
h2,
.h3,
h3,
.h4,
h4,
.h5,
h5,
.h6,
h6 {
  margin: 97.5px 0 37.5px;
  font-family: "heebo", sans-serif;
  line-height: calc(2px + 2ex + 2px);
}

.h1 a,
h1 a,
.h2 a,
h2 a,
.h3 a,
h3 a,
.h4 a,
h4 a,
.h5 a,
h5 a,
.h6 a,
h6 a {
  color: inherit;
  font-weight: inherit;
  text-decoration: none;
}

.h1 a:hover,
h1 a:hover,
.h2 a:hover,
h2 a:hover,
.h3 a:hover,
h3 a:hover,
.h4 a:hover,
h4 a:hover,
.h5 a:hover,
h5 a:hover,
.h6 a:hover,
h6 a:hover {
  color: var(--color-text-link);
}

.h1,
h1 {
  font-size: 57.12px;
  font-size: 3.57rem;
}

.h2,
h2 {
  font-size: 49.72px;
  font-size: 3.108rem;
}

.h3,
h3 {
  font-size: 43.94px;
  font-size: 2.746rem;
}

.h4,
h4 {
  font-size: 33.8px;
  font-size: 2.112rem;
}

.h5,
h5 {
  font-size: 26px;
  font-size: 1.625rem;
}

.h6,
h6 {
  font-size: 20px;
  font-size: 1.25rem;
}

/*******************************************************************************
//
//  #PARAGRAPHS
//
*******************************************************************************/

p {
  margin: 0 0 37.5px;
}

/*******************************************************************************
//
//  #ANCHORS
//
*******************************************************************************/

a {
  color: var(--color-text-link);
  outline: 0;
  transition: color 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

a:focus,
a:hover {
  color: var(--color-text);
}

a:focus {
  outline: 1px dotted var(--color-accent);
  outline-offset: 4px;
}

/*******************************************************************************
//
//  #FINE PRINTS
//
*******************************************************************************/

abbr[title],
abbr[data-original-title] {
  border-bottom: 1px dotted var(--color-border);
  cursor: help;
}

small {
  font-size: 80%;
}

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

b,
strong {
  font-weight: 700;
}

em {
  font-style: italic;
}

/*******************************************************************************
//
//  #BLOCKQUOTE
//
*******************************************************************************/

blockquote {
  padding: 0.5em 1em;
  margin: 0 0 37.5px;
  border-left: 0.2em solid var(--color-border);
  white-space: normal;
}

blockquote blockquote {
  margin-bottom: 0;
}

/*******************************************************************************
//
//
//
//  #VIDEO
//
//
//
*******************************************************************************/

video {
  display: block;
  width: 100%;
}

/*******************************************************************************
//
//
//
//  #IFRAMES
//
//
//
*******************************************************************************/

iframe {
  margin: 0 0 37.5px;
}

/*******************************************************************************
//
//
//
//  #PRISMJS
//
//
//
*******************************************************************************/

pre[class*='language-'],
code[class*='language-'] {
  color: #ef596f;
  direction: ltr;
  font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
  font-size: 16px;
  hyphens: none;
  line-height: 1.5;
  tab-size: 4;
  text-align: left;
  text-shadow: none;
  white-space: pre;
  word-break: normal;
  word-spacing: normal;
}

pre[class*='language-']::selection,
code[class*='language-']::selection,
pre[class*='language-']::mozselection,
code[class*='language-']::mozselection {
  text-shadow: none;
  background: gold;
}

pre[class*='language-'] {
  padding: 1em;
  margin: 0.5em 0;
  background: #000000;
  border: 1px solid #47474d;
  border-radius: 8px;
  overflow: auto;
}

:not(pre) > code[class*='language-'] {
  padding: 0.1em 0.3em;
  color: gold;
  background: #000000;
  border-radius: 0.3em;
}

.prism-titlename {
  margin: -1em -1em 1em;
  color: #f1f1f1;
  background: #47474d;
}

/*******************************************************************************
//
//  #TOKENS
//
*******************************************************************************/

.namespace {
  opacity: 0.7;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: #5c6370;
}

.token.punctuation {
  color: #bbbbbb;
}

.token.property,
.token.tag,
.token.boolean,
.token.number,
.token.constant,
.token.symbol,
.token.deleted {
  color: #bbbbbb;
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
  color: #89ca78;
}

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
  color: #2bbac5;
  background: #000000;
}

.token.atrule,
.token.attr-value,
.token.keyword {
  color: #d55fde;
}

.token.function {
  color: #2bbac5;
}

.token.regex,
.token.important,
.token.variable {
  color: #89ca78;
}

.token.important,
.token.bold {
  font-weight: bold;
}

.token.italic {
  font-style: italic;
}

.token.entity {
  cursor: help;
}

/*******************************************************************************
//
//  #LINE HIGHLIGHTING
//
*******************************************************************************/

pre[data-line] {
  position: relative;
}

pre[class*='language-'] > code[class*='language-'] {
  position: relative;
  z-index: 1;
}

.line-highlight {
  padding: inherit 0;
  margin-top: 1em;
  position: absolute;
  left: 0;
  right: 0;
  z-index: 0;
  line-height: inherit;
  white-space: pre;
  background: #262626;
  box-shadow: inset 5px 0 0 gold;
  pointer-events: none;
}

/*******************************************************************************
//
//
//
//  #CONTAINER
//
//
//
*******************************************************************************/

.o-container {
  width: 100%;
  max-width: 1920px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  position: relative;
}

.o-container:after {
  content: "";
  display: table;
  clear: both;
}

/*******************************************************************************
//
//
//
//  #GRID
//
//
//
*******************************************************************************/

.o-grid {
  /***************************************************************************
    //
    //  #COLUMN
    //
    ***************************************************************************/
  /***************************************************************************
    //
    //  #ROW
    //
    ***************************************************************************/
}

.o-grid .o-grid__column {
  float: left;
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  position: relative;
}

.o-grid .o-grid__row {
  margin-left: -15px;
  margin-right: -15px;
}

.o-grid .o-grid__row:after {
  content: "";
  display: table;
  clear: both;
}

/*******************************************************************************
//
//
//
//  #LIST BARE
//
//
//
*******************************************************************************/

.o-list-bare {
  margin-left: 0;
  list-style: none;
}

.o-list-bare.-inline .o-list-bare__item,
.o-list-bare.-inline li {
  display: inline-block;
  margin-bottom: 0;
}

/*******************************************************************************
//
//
//
//  #MEDIA
//
//
//
*******************************************************************************/

.o-media,
.o-media__content {
  overflow: hidden;
}

.o-media__image {
  float: left;
  margin-right: 10px;
}

.o-media__image img {
  display: block;
}

/*******************************************************************************
//
//
//
//  #RATIO
//
//
//
*******************************************************************************/

.o-ratio {
  display: block;
  position: relative;
  /***************************************************************************
    //
    //  VARIANTS
    //
    ***************************************************************************/
}

.o-ratio:before {
  content: '';
  display: block;
  width: 100%;
  padding-bottom: 100%;
}

.o-ratio .o-ratio__content,
.o-ratio .o-ratio > embed,
.o-ratio .o-ratio > iframe,
.o-ratio .o-ratio > object {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.o-ratio.-contain-image > .o-ratio__image {
  width: auto;
  max-width: 100%;
  height: auto;
  max-height: 100%;
  margin: auto;
}

.o-ratio.-ratio-16by9::before {
  padding-bottom: 56.25%;
}

/*******************************************************************************
//
//
//
//  #MEDIA QUERIES
//  -> Styles for ../../../scripts/main/utilities/media-queries.js
//
//
//
*******************************************************************************/

.js-media-query {
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  position: absolute;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
  z-index: 0;
}

/*******************************************************************************
//
//
//
//  #BUTTON
//
//
//
*******************************************************************************/

.c-button {
  font-size: 14px;
  font-size: 0.875rem;
  line-height: 21px;
  line-height: 1.312rem;
  padding: 0.75em;
  display: inline-block;
  color: var(--color-button-text);
  font-family: "din-condensed", sans-serif;
  letter-spacing: 0.1em;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  background: var(--color-button-background);
  border: 0;
  cursor: pointer;
  outline: 0;
}

.c-button:active,
.c-button:focus,
.c-button:hover {
  color: var(--color-button-text-active);
  text-decoration: none;
  background: var(--color-button-background-active);
}

.c-button.disabled,
.c-button[disabled] {
  cursor: default;
  opacity: 0.5;
}

/*******************************************************************************
//
//
//
//  #CONTAINER
//
//
//
*******************************************************************************/

html.js .c-container {
  width: 100%;
  position: absolute;
  overflow: hidden;
  backface-visibility: hidden;
