/*******************************************************************************
//
//
//
//  #FORM
//
//
//
*******************************************************************************/

.c-form {
  /***************************************************************************
    //  #CHECKBOX AND RADIO
    ***************************************************************************/
  /*
    *
    *    HTML
    *
    *    <div class="c-form__checkbox">
    *        <input id="checkbox-1" name="checkbox-1" type="checkbox" value="1">
    *        <label for="checkbox-1">Checkbox 1</label>
    *    </div>
    *
    *    <div class="c-form__radio">
    *        <input id="radio-1" name="radio" type="radio" value="1">
    *        <label for="radio-1">Radio 1</label>
    *    </div>
    *
    */
}

.c-form .c-form__group {
  margin: 0 0 37.5px;
}

.c-form .c-form__checkbox,
.c-form .c-form__radio {
  display: inline-block;
  padding-left: 1.312em;
  margin: 0 15px 7.5px 0;
  position: relative;
}

.c-form .c-form__checkbox input[type='checkbox'],
.c-form .c-form__radio input[type='radio'] {
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
  z-index: 1;
}

.c-form .c-form__checkbox input[type='checkbox'] + label,
.c-form .c-form__radio input[type='radio'] + label {
  display: block;
  padding: 0 0 0 0.25em;
  margin: 0;
  overflow: hidden;
  text-overflow: ellipsis;
}

.c-form .c-form__checkbox input[type='checkbox'] + label:after,
.c-form .c-form__checkbox input[type='checkbox'] + label:before,
.c-form .c-form__radio input[type='radio'] + label:after,
.c-form .c-form__radio input[type='radio'] + label:before {
  content: '';
  display: block;
  width: 1em;
  height: 1em;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
}

.c-form .c-form__radio input[type='radio'] + label:after,
.c-form .c-form__radio input[type='radio'] + label:before {
  border-radius: 50%;
}

.c-form .c-form__checkbox input[type='checkbox'] + label:before,
.c-form .c-form__radio input[type='radio'] + label:before {
  content: '';
  border: 1px solid var(--color-border);
}

.c-form .c-form__checkbox input[type=checkbox]:checked + label:after,
.c-form .c-form__radio input[type=radio]:checked + label:after {
  content: '';
  background-color: var(--color-accent);
  border: 1px solid var(--color-border);
}

.c-form .c-form__select {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0.5em 1.625em 0.5em 0.5em;
  margin: 0;
  appearance: none;
  background-color: #fff;
  background-image: url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIxMDAiIGhlaWdodD0iNTAiIHZpZXdCb3g9IjAgMCAxMDAgNTAiPgogIDxwb2x5Z29uIHBvaW50cz0iMCAwIDUwIDUwIDEwMCAwIDAgMCIvPgo8L3N2Zz4K");
  background-repeat: no-repeat;
  background-position: right 0.5em top 50%;
  background-size: 0.625em auto;
  border: 1px solid var(--color-border);
  border-radius: 0;
}

.c-form .c-form__select::-ms-expand {
  display: none;
}

.c-form .c-form__select:focus {
  border-color: var(--color-border-focus);
  outline: 0;
}

.c-form .c-form__select option {
  font-weight: normal;
}

