//
//  #HEADER
//
//
//
*******************************************************************************/

.c-header {
  display: flex;
  align-items: center;
  height: 80px;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 100;
  backface-visibility: hidden;
  will-change: transform;
  /***************************************************************************
    //  #IS PAGE READY
    ***************************************************************************/
  /***************************************************************************
    //
    //  #LOGO
    //
    ***************************************************************************/
  /***************************************************************************
    //
    //  #NAV
    //
    ***************************************************************************/
  /***************************************************************************
    //
    //  #NAV TRIGGER
    //
    ***************************************************************************/
  /***************************************************************************
    //
    //  #THEME TOGGLE
    //
    ***************************************************************************/
}

html.js .c-header {
  opacity: 0;
  transform: translate(0, -3.125rem);
  transition: opacity 0.75s cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s, transform 0.75s cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s;
}

html.js.is-page-ready .c-header {
  opacity: 1;
  transform: translate(0, 0);
}

html.js.is-page-ready .c-header.-is-active {
  transition: opacity 0.75s cubic-bezier(0.785, 0.135, 0.15, 0.86), transform 0.75s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

html.js.is-page-ready .c-header.-is-hidden {
  opacity: 0;
  transform: translate(0, -3.125rem);
}

.c-header .c-header__logo {
  display: inline-block;
  position: relative;
}

.c-header .c-header__logo svg {
  fill: var(--color-primary);
}

.c-header .c-header__logo:focus svg {
  fill: var(--color-accent);
}

.c-header .c-header__nav ul {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  list-style: none;
}

.c-header .c-header__nav ul > li {
  position: relative;
}

.c-header .c-header__nav ul > li > a,
.c-header .c-header__nav ul > li > label {
  display: block;
  color: var(--color-primary);
  font-weight: 700;
  line-height: 1;
  text-decoration: none;
}

.c-header .c-header__nav ul > li > a:active,
.c-header .c-header__nav ul > li > a:focus,
.c-header .c-header__nav ul > li > a:hover,
.c-header .c-header__nav ul > li > label:active,
.c-header .c-header__nav ul > li > label:focus,
.c-header .c-header__nav ul > li > label:hover {
  color: var(--color-accent);
}

.c-header .c-header__nav-trigger {
  overflow: hidden;
  text-indent: 150%;
  white-space: nowrap;
  width: 30px;
  height: 20px;
  padding: 0;
  margin-left: auto;
  position: relative;
  z-index: 105;
  color: var(--color-primary);
  line-height: 20px;
  background: none;
  border: none;
}

.c-header .c-header__nav-trigger span {
  display: block;
  width: 100%;
  height: 2px;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(0%, -50%);
  background: var(--color-primary);
}

.c-header .c-header__nav-trigger span,
.c-header .c-header__nav-trigger span:after,
.c-header .c-header__nav-trigger span:before {
  transition: all 0.3s cubic-bezier(0.19, 1, 0.22, 1);
}

.c-header .c-header__nav-trigger span:after,
.c-header .c-header__nav-trigger span:before {
  content: '';
  display: block;
  width: 100%;
  height: 2px;
  position: absolute;
  right: 0;
  background: var(--color-primary);
}

.c-header .c-header__nav-trigger span:after {
  bottom: 8px;
}

.c-header .c-header__nav-trigger span:before {
  top: 8px;
}

.c-header .c-header__nav-trigger[aria\-expanded=true] span {
  background: rgba(0, 0, 0, 0);
}

.c-header .c-header__nav-trigger[aria\-expanded=true] span:after {
  width: 100%;
  bottom: 0;
  transform: rotate(-45deg);
  background: mvar(--color-primary);
}

.c-header .c-header__nav-trigger[aria\-expanded=true] span:before {
  width: 100%;
  top: 0;
  transform: rotate(45deg);
  background: var(--color-primary);
}

.c-header .c-header__theme-toggle {
  margin: 0;
  cursor: pointer;
}

.c-header .c-header__theme-toggle input:focus + span {
  color: var(--color-accent);
  outline: 1px dotted var(--color-accent);
  outline-offset: 4px;
}

