}

html.js.is-page-ready .c-jumbotron .c-jumbotron__footer {
  opacity: 1;
  transform: translate(0, 0);
}

.c-jumbotron .c-jumbotron__main {
  width: 100%;
  max-width: 1920px;
}

.c-jumbotron .c-jumbotron__scroll-prompt {
  width: 2px;
  height: 0;
  position: absolute;
  top: 50%;
  right: 60px;
  margin-top: -125px;
  background: var(--color-tertiary);
  transform-origin: center top;
}

html.js.is-page-ready .c-jumbotron .c-jumbotron__scroll-prompt {
  animation: pulse 3s infinite 1s;
}

.c-jumbotron .c-jumbotron__statement {
  font-size: calc(40px + 100 * ((100vw - 320px) / 1600));
  margin: 0;
  color: var(--color-primary);
  line-height: calc(8px + 2ex + 8px);
  /***********************************************************************
        //  #IS PAGE READY
        ***********************************************************************/
}

html.js .c-jumbotron .c-jumbotron__statement {
  transform: translate(0, -3.125rem);
  transition: opacity 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.25s, transform 0.75s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

html.js.is-page-ready .c-jumbotron .c-jumbotron__statement {
  opacity: 1;
  transform: translate(0, 0);
}

