*******************************************************************************/

@keyframes pulse {
  0% {
    height: 0;
    opacity: 1;
  }

  50% {
    height: 250px;
    opacity: 1;
  }

  90% {
    height: 250px;
    opacity: 0;
  }

  100% {
    height: 0;
    opacity: 0;
