@media screen and (max-width: 47.938em) {
  .c-footer {
    padding: 15px 0;
  }

  .c-header .c-header__nav {
    height: 100vh;
    padding: 30px;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 100;
    background: var(--color-background);
    flex-direction: column;
    justify-content: center;
  }

  .c-header .c-header__nav ul {
    height: 100%;
    flex-direction: column;
  }

  .c-header .c-header__nav ul > li {
    text-align: center;
  }

  .c-header .c-header__nav ul > li:not(:last-child) {
    margin-bottom: 37.5px;
  }

  .c-header .c-header__nav ul > li > a,
  .c-header .c-header__nav ul > li > label {
    font-size: 42px;
    font-size: 2.625rem;
    line-height: 1.143;
    font-family: "din-condensed", sans-serif;
    font-weight: normal;
    letter-spacing: 0.2em;
    text-transform: uppercase;
  }

  .c-header .c-header__nav .c-header__menu-seperator {
    display: none;
  }

  .c-jumbotron .c-jumbotron__footer {
    padding: 15px 0;
  }

  .c-jumbotron .c-jumbotron__footer .c-jumbotron__footer-social {
    justify-content: center;
  }

  .c-jumbotron .c-jumbotron__scroll-prompt {
    display: none;
  }

  .c-social-icons {
    justify-content: center;
  }
}

@media screen and (max-width: 59.938em) {
  .c-jumbotron .o-section-heading {
    text-align: center;
  }

  .c-jumbotron .c-jumbotron__statement {
    text-align: center;
  }

  .o-section-heading {
    margin: 0 0 37.5px;
    text-align: center;
  }
}

@media screen and (max-width: 19.938em) {
  .c-jumbotron .c-jumbotron__statement {
    font-size: 40px;
  }
}

@media screen and (min-width: 37.5em) {
  .c-post .wp-block-columns .wp-block-column:nth-child(2n) {
    margin-left: 30px;
  }
}